<template>
  <div>
    <button @click="inCounter">Add 1</button>
    <p>{{ counter }}</p>
    <button @click="outCounter">Minus 1</button>
    <p>{{ num }}</p>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      counter: 0,
      num: 100
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    inCounter() {
      this.counter += 1
    },
    outCounter() {
      this.num -= 1
    }
  }
}
</script>
