<template>
  <div>
    <select name="" id="" @change="changeCity()" v-model="selectedCity">
      <option value="">===도시선택===</option>
      <option
        :value="city.cityCode"
        :key="city.cityCode"
        v-for="city in cityList"
      >
        {{ city.title }}
      </option>
    </select>
    <select name="" id="">
      <option value="">===동선택===</option>
      <option
        :value="dong.dongCode"
        :key="dong.dongCode"
        v-for="dong in selcetedDongList"
      >
        {{ dong.dongTitle }}
      </option>
    </select>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      selectedCity: '',
      cityList: [
        { cityCode: '02', title: '서울' },
        { cityCode: '032', title: '인천' },
        { cityCode: '041', title: '천안' },
        { cityCode: '064', title: '제주' }
      ],
      dongList: [
        { cityCode: '02', dongCode: '01', dongTitle: '구로1동' },
        { cityCode: '02', dongCode: '02', dongTitle: '구로2동' },
        { cityCode: '02', dongCode: '03', dongTitle: '구로3동' },
        { cityCode: '032', dongCode: '01', dongTitle: '간석1동' },
        { cityCode: '032', dongCode: '02', dongTitle: '간석2동' },
        { cityCode: '032', dongCode: '03', dongTitle: '간석3동' },
        { cityCode: '041', dongCode: '01', dongTitle: '두정1동' },
        { cityCode: '041', dongCode: '02', dongTitle: '두정2동' },
        { cityCode: '041', dongCode: '03', dongTitle: '두정3동' },
        { cityCode: '064', dongCode: '01', dongTitle: '서귀포1동' },
        { cityCode: '064', dongCode: '02', dongTitle: '서귀포2동' },
        { cityCode: '064', dongCode: '03', dongTitle: '서귀포3동' }
      ],
      selcetedDongList: []
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    changeCity() {
      this.selcetedDongList = this.dongList.filter(
        (dong) => dong.cityCode === this.selectedCity
      )
    }
    // arrow 함수 사용할때 오류 나지 않게 조심 : ()괄호안에 넣기
  }
}
</script>
<style scoped>
select {
  margin: 5px;
}
</style>
