<template>
  <div>
    <!-- <input type="search" @keyup="checkEnter($event)" v-model="searchText" /> -->
    <!-- 다른 변수명이 아닌 event로 사용해야함 -->
    <input type="search" @keyup.enter="doSearch" v-model="searchText" />
    <button @click="doSearch">조회</button>
  </div>
</template>
<script>
// @keyup.enter ==> 이런식으로 사용
// .delete (BackSpace, Delete), .esc(Esc), .space(Space), .tab, .left, .up, .right, .down
export default {
  components: {},
  data() {
    return {
      searchText: ''
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    doSearch() {
      console.log(this.searchText)
    },
    checkEnter(event) {
      if (event.keyCode === 13) {
        this.doSearch()
      }
    }
  }
}
</script>
<style scoped>
input {
  margin: 5px;
}
</style>
